.award {
    padding-top: 60px;
    padding-bottom: 10px;
    text-align: center;
    &__title {
        @extend .info__title;
    }
    &__item {
        margin-bottom: 20px;
        border: 2px solid transparent;
        transition: all 0.3s;
        &:hover {
            border-color: $red-5;
        }
    }
    &__bg {
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: $bsd-1;
        img {
            width: 50px;
            height: 50px;
            transition: all 0.3s;
        }
        &:hover {
            img {
                transform: scale(1.3) rotate(360deg);
            }
        }
    }
}

.post {
    padding-top: 30px;
    padding-bottom: 10px;
    &__item {
        margin-bottom: 20px;
        padding: 30px;
        background: #fff;
    }
    &__text1 {
        max-height: 53px;
        overflow: hidden;
        margin-bottom: 20px;
        font-family: TeXGyreSchola-Regular;
        font-size: sz(40);
        line-height: sz(53);
        letter-spacing: 0;
        color: $red-3;
    }
    &__text2 {
        @extend .info__text;
        max-height: 115px;
        overflow: hidden;
        margin-bottom: 30px;
    }
    &__text3 {
        font-family: Roboto-Bold;
        font-size: sz(18);
        line-height: sz(24);
        letter-spacing: sz(0.57);
        color: $red-2;
        &:hover {
            text-decoration: underline;
            color: $red-5;
        }
    }
}

.video {
    @extend .award;
    padding-top: 30px;
    &__title {
        @extend .info__title;
    }
    &__item {
        @extend .award__item;
    }
    &__bg {
        @extend .award__bg;
        img {
            width: 40px;
            height: 40px;
            &.play-90 {
                width: 90px;
                height: 90px;
            }
        }
    }
}

.facilities {
    padding-top: 30px;
    padding-bottom: 30px;
    text-align: center;
    &__content {
        padding-top: 30px;
        background: #fff;
    }
    &__title {
        @extend .info__title;
    }
    &__item {
        width: (100%)/3;
        padding: 60px 30px 30px;
        &:nth-child(1) {
            border-top: 2px solid $grey-4;
            border-right: 1px solid $grey-4;
            border-bottom: 2px solid transparent;
            border-left: 2px solid transparent;
        }
        &:nth-child(2) {
            border-top: 2px solid $grey-4;
            border-right: 2px solid $grey-4;
            border-bottom: 2px solid transparent;
            border-left: 2px solid $grey-4;
        }
        &:nth-child(3) {
            border-top: 2px solid $grey-4;
            border-right: 2px solid transparent;
            border-bottom: 2px solid transparent;
            border-left: 1px solid $grey-4;
        }
    }
    &__img {
        min-height: 100px;
        margin-bottom: 20px;
        img {
            max-width: 100%;
        }
    }
    &__text1 {
        margin-bottom: 20px;
        font-family: TeXGyreSchola-Regular;
        font-size: sz(28);
        line-height: sz(43);
        letter-spacing: sz(1.24);
        color: $black-1;
    }
    &__text2 {
        margin-bottom: 15px;
        font-size: sz(24);
        letter-spacing: 0;
        color: $red-2;
        &:hover {
            text-decoration: underline;
            color: $red-5;
        }
    }
}

.partners {
    padding-top: 30px;
    padding-bottom: 20px;
    text-align: center;
    &__title {
        @extend .info__title;
    }
    &__group {
        margin-bottom: 10px;
        padding: 20px 20px 5px;
        background: #fff;
    }
    &__text {
        margin-bottom: 20px;
        font-family: Roboto-Bold;
        font-size: sz(20);
        line-height: sz(26);
        letter-spacing: 0;
        color: $grey-1;
    }
}

@media (max-width: 991px) {
    .facilities {
        &__item {
            width: 80%;
            margin-bottom: 30px;
            &:nth-child(1),
            &:nth-child(2),
            &:nth-child(3) {
                border: 2px solid $grey-4;
            }
        }
    }
}

@media (max-width: 767px) {
    .video {
        &__bg {
            img {
                &.play-90 {
                    width: 40px;
                    height: 40px;
                }
            }
        }
    }
}

@media (max-width: 575px) {
    .post {
        text-align: center;
    }
    .facilities {
        &__item {
            width: 95%;
        }
    }
}