.academy-info {
    padding-top: 60px;
}

.academy-video {
    padding-top: 30px;
    padding-bottom: 30px;
    &__title {
        @extend .info__title;
        text-align: center;
    }
    &__content {
        padding-top: 60px;
        padding-bottom: 40px;
        background: #000;
    }
    &__item {
        @extend .award__item;
    }
    &__bg {
        @extend .award__bg;
        img {
            width: 32px;
            height: 32px;
        }
        &.active {
            filter: brightness(50%);
            img {
                display: none;
            }
        }
    }
}

@media (max-width: 575px) {
    .academy-video {
        &__bg {
            img {
                width: 18px;
                height: 18px;
            }
        }
    }
}