.img-golf-about {
    padding-top: 60px;
    img {
        width: 100%;
        border-radius: 20px;
    }
    &__content {
        max-width: 515px;
        margin: auto;
    }
    &__title {
        @extend .info__title;
        margin-bottom: 20px;
    }
    &__text {
        @extend .info__text;
    }
    &__info {
        max-width: 820px;
        margin: auto;
        padding-top: 36px;
        text-align: center;
    }
    &__btn {
        width: 206px;
        height: 50px;
        margin: 30px auto;
        font-size: sz(16);
        text-transform: uppercase;
        letter-spacing: sz(0.51);
    }
}