.getting {
    padding-top: 60px;
    padding-bottom: 30px;
    &__title {
        @extend .info__title;
        margin-bottom: 20px;
        text-align: center;
    }
    &__text {
        margin-bottom: 60px;
        @extend .info__text;
        text-align: center;
        text-transform: uppercase;
    }
    &__item {
        margin-bottom: 18px;
    }
    &__blade {
        margin-bottom: 12px;
        @extend .info__text;
        transition: all 0.3s;
        &:hover {
            color: $red-5;
            .getting__line {
                border-color: $red-5;
            }
        }
        .fa-circle {
            margin-right: 8px;
            font-size: sz(12);
            color: $red-4;
        }
        .fa-map-marker {
            margin-right: 8px;
            margin-bottom: 2px;
            font-size: sz(18);
            color: $red-4;
        }
    }
    &__text-first {
        margin-right: 20px;
    }
    &__text-last {
        margin-left: 20px;
    }
    &__line {
        flex: 1;
        width: 100%;
        border-bottom: 1px solid $grey-2;
        transition: all 0.3s;
    }
}