.driving-info {
    padding-top: 60px;
    padding-bottom: 20px;
    &__title {
        @extend .info__title;
        text-align: center;
    }
    &__text1 {
        @extend .info__text;
    }
    &__text2 {
        @extend .info__text;
        font-family: Roboto-Bold;
    }
    ul {
        padding-left: 20px;
    }
    li {
        padding-left: 10px;
    }
}