.footer {
    padding-top: 30px;
    &__content {
        padding: 22px 30px 0;
        background: #fff;
    }
    &__title {
        margin-bottom: 6px;
        font-family: Roboto-Medium;
        font-size: sz(16);
        line-height: sz(24);
        text-transform: uppercase;
        color: $black-1;
    }
    &__text {
        font-size: sz(15);
        line-height: sz(21);
        color: $black-1;
    }
    &__link {
        font-size: sz(15);
        line-height: sz(24);
        color: $grey-1;
        transition: all 0.3s;
        &:hover {
            text-decoration: underline;
            color: $red-5;
        }
    }
    &__logo1 {
        width: 85px;
        margin-left: 7px;
    }
    &__logo2 {
        width: 128px;
        margin-top: 14px;
        margin-bottom: 24px;
    }
    &__btn-yt {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 154px;
        height: 36px;
        margin-top: 4px;
        margin-bottom: 22px;
        font-size: sz(14);
        color: #fff;
        background: $red-2;
        transition: all 0.3s;
        &:hover {
            color: #fff;
            background: $red-5;
        }
        img {
            margin-right: 8px;
        }
    }
}

@media (max-width:767px) {
    .footer {
        &__content {
            padding: 20px 15px 0;
        }
    }
}