.press {
	&__head {
		display: flex;
    	justify-content: center;
    	margin: 60px 0 40px;
	}
	&__text2 {
		font-family: TeXGyreSchola-Regular;
		font-size: 40px;
		color: #8A1512;
		margin-bottom: 20px;
	}
	&__content {
		margin-bottom: 30px;
	}
	&__item {
		background: #fff;
		margin-bottom: 20px;
	}
	&__logo {
		padding: 0 20px;
		border-bottom: 2px solid #D2D2D2;
		text-align: center;
		img {
			width: 100%;
			height: 130px;
		}
	}
	&__info {
		padding: 20px 20px 30px;
		font-size: 18px;
		color: #212121;
	}
	&__title {
		font-family: Roboto-Bold;
		font-size: 24px;
		color: #1A1A1A;
		margin-bottom: 13px;
		height: 70px;
		overflow: hidden;
	}
	&__date {
		margin-bottom: 15px;
		height: 27px;
    	overflow: hidden;
	}
	&__text {
		margin-bottom: 15px;
		height: 108px;
    	overflow: hidden;
	}
	&__link {
		font-family: Roboto-Bold;
		color: #8A1512;
		&:hover {
            text-decoration: underline;
            color: $red-5;
        }
	}
	&__loading {
		width: 34px;
		height: 32px;
	}
	.fa-angle-down {
		font-size: 16px;
		color: #D4D4D4;
	}
	.ui.selection.dropdown {
		display: flex;
		justify-content: space-between;
		min-width: 120px;
		border-radius: 0;
		padding: 12px 10px 10px;
		margin-right: 10px;
	}
	.ui.selection.dropdown:focus,
	.ui.selection.active.dropdown {
		border-color: #8A1512;
	}
	.ui.selection.active.dropdown .menu {
		border-color: #8A1512;
	}
	.ui.selection.dropdown .menu>.item {
		font-size: 14px;
	}
	.ui.default.dropdown:not(.button)>.text, .ui.dropdown:not(.button)>.default.text {
		font-size: 14px;
		color: #1A1A1A;
	}
}

@media (max-width: 767px) and (min-width: 400px) {
	.press__logo {
		img {
			width: 357px;
		}
	}
}