.wrapper {
    background: $grey-4;
}

.btn-red {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid $red-2;
    color: #fff;
    background: $red-2;
    transition: all 0.3s;
    &:hover {
        color: $red-2;
        background: #fff;
    }
}

.zoom-outer {
    overflow: hidden;
    &:hover {
        .zoom-inner {
            transform: scale(1.25)
        }
    }
}

.zoom-inner {
    transition: 0.5s;
}

@media (min-width: 992px) {
    .w-lg-75 {
        width: 75%;
    }
}

