.mess {
    &__item {
        padding-top: 30px;
        padding-bottom: 30px;
    }
    &__title {
        @extend .info__title;
        margin-bottom: 20px;
    }
    &__text {
        @extend .info__text;
    }
    a {
        color: $red-3;
        &:hover {
            text-decoration: underline;
            color: $red-5;
        }
        &.mess__text {
            font-family: Roboto-Bold;
        }
        .fa {
            margin-right: 10px;
        }
    }
    ul {
        padding-left: 24px;
    }
    li {
        padding-left: 8px;
    }
}

@media (min-width:992px) {
    .mess {
        &__max {
            max-width: 540px;
        }
        &__img {
            width: 50%;
        }
        &__content {
            width: 50%;
        }
    }
}

@media (max-width:991px) {
    .mess {
        &__max {
            max-width: 767px;
        }
    }
}