.rate {
    padding-top: 60px;
    padding-bottom: 36px;
    &__title {
        @extend .info__title;
        margin-bottom: 20px;
    }
    &__text {
        @extend .info__text;
        margin-bottom: 20px;
    }
    &__btn {
        width: 205px;
        height: 50px;
        font-size: sz(16);
        letter-spacing: sz(0.51);
    }
}

.rate-pmt {
    &__title {
        @extend .info__title;
        margin-bottom: 15px;
    }
    &__text1 {
        margin-bottom: 10px;
        font-family: Roboto-Bold;
        font-size: sz(24);
        line-height: sz(32);
        color: $black-1;
    }
    &__text2 {
        @extend .info__text;
        margin-bottom: 10px;
        li {
            margin-bottom: 3px;
        }
    }
    &__text3 {
        @extend .info__text;
        margin-bottom: 10px;
        font-family: Roboto-Bold;
    }
    &__text4 {
        @extend .info__text;
        margin-bottom: 10px;
        color: $grey-1;
    }
    &__text5 {
        @extend .info__text;
        margin-bottom: 0;
        color: $red-3;
        &:hover {
            text-decoration: underline;
            color: $red-5;
        }
    }
    &__item {
        @extend .award__item;
        margin-bottom: 30px;
    }
    &__bg {
        @extend .award__bg;
        font-size: sz(32);
        text-shadow: $tsd-1;
        color: #fff;
        &:hover {
            color: #fff;
        }
    }
}