.about-course {
    padding: 60px 0 15px;
}

.about-video {
    padding-top: 30px;
    padding-bottom: 10px;
    &__item {
        @extend .award__item;
    }
    &__bg {
        @extend .award__bg;
        font-size: sz(40);
        letter-spacing: sz(1.14);
        line-height: sz(53);
        text-shadow: 0 4px 2px rgba(0, 0, 0, 0.73);
        color: #FFFFFF;
        &:hover {
            color: #fff;
        }
        img {
            width: 60px;
            height: 60px;
            margin-right: 20px;
        }
    }
}

@media (max-width: 575px) {
    .about-video {
        &__bg {
            font-size: 24px;
            img {
                width: 40px;
                height: 40px;
                margin-right: 15px;
            }
        }
    }
}