.contact {
    padding-top: 60px;
    &__title {
        @extend .info__title;
        margin-bottom: 60px;
        text-align: center;
    }
    &__text1 {
        margin-bottom: 20px;
        font-family: Roboto-Bold;
        font-size: sz(20);
        line-height: sz(25);
        color: $black-1;
    }
    &__text2 {
        margin-bottom: 10px;
        line-height: sz(20);
        color: $grey-1;
    }
    input {
        width: 100%;
        margin-bottom: 4px;
        padding-bottom: 10px;
        border-top: none;
        border-right: none;
        border-left: none;
        border-bottom: 1px solid $red-3;
        background: transparent;
        &:focus {
            outline: none;
        }
    }
    .btn-red {
        width: 110px;
        height: 50px;
        margin-top: 10px;
        margin-bottom: 30px;
    }
    &__text3 {
        font-family: Roboto-Bold;
        line-height: sz(22);
        color: $black-2;
    }
    &__text3 {
        line-height: sz(22);
        color: $black-2;
    }
}