.gallery {
	padding: 20px 0 10px;
	&__content {
		margin-top: 40px;
	}
	&__title {
		font-family: TeXGyreSchola-Regular;
		font-size: 40px;
		color: #8A1512;
		text-align: center;
		margin-bottom: 30px;
	}
	&__mg {
		margin-bottom: 20px;
	}
}

@media (max-width: 767px) {
	.gallery {
		&__mg {
			margin-bottom: 10px;
		}
		.row {
			margin-left: -4px;
			margin-right: -4px;
		}
		.col-4 {
			padding-left: 4px;
			padding-right: 4px;
		}
	}
}