.banner {
    height: 880px;
    &--md {
        height: 660px;
    }
    &__bottom {
        margin-top: 220px;
    }
    &__title {
        margin-bottom: 10px;
        font-family: TeXGyreSchola-Regular;
        font-size: sz(72);
        line-height: sz(95);
        letter-spacing: sz(-2.9);
        text-shadow: 0 2px 2px rgba(#000, 0.5);
        color: #fff;
    }
    &__des {
        font-size: sz(15);
        line-height: sz(28);
        text-transform: uppercase;
        text-shadow: 0 2px 4px rgba(0, 0, 0, 0.50);
        color: #FFF;
    }
}

@media (min-width:992px) {
    .banner {
        &__right {
            margin-left: 50%;
        }
    }
}

@media (max-width: 767px) {
    .banner {
        height: 767px;
    }
}

@media (max-width: 575px) {
    .banner {
        height: 575px;
        &__title {
            font-size: sz(36);
            line-height: sz(48);
            letter-spacing: 0;
        }
        &__des {
            font-size: sz(12);
            line-height: sz(24);
            letter-spacing: 0;
        }
    }
}