.header {
    position: relative;
    background-color: $black-2;
    &__logo {
        img {
            width: 46px;
            height: 17px;
        }
        span {
            margin-right: 12px;
            margin-left: 12px;
            font-size: sz(14);
            color: $grey-5;
        }
        a {
            font-size: sz(14);
            letter-spacing: 0;
            line-height: 19px;
            color: $grey-5;
            &:hover {
                color: #fff;
            }
        }
    }
    .fa {
        margin-right: sz(20);
        color: $grey-5;
        transition: all 0.3s;
        &:hover {
            color: #fff;
        }
    }
    &__btn {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 153px;
        height: 42px;
        font-family: Roboto-Medium;
        font-size: sz(14);
        &--light {
            color: $red-3;
            background: #fff;
            &:hover {
                color: $red-5;
                background: $grey-4;
            }
        }
        &--dark {
            color: #fff;
            background: $red-3;
            &:hover {
                color: #fff;
                background: $red-5;
            }
        }
    }
    &__menu {
        position: absolute;
        z-index: 1;
        width: 100%;
        min-height: 144px;
        padding-top: 8px;
        background-image: linear-gradient(0deg, rgba(#000, 0) 0%, rgba(#000, 0.4) 100%);
        img {
            position: absolute;
            top: 20px;
            left: 30px;
            z-index: -1;
            width: 160px;
            height: 160px;
        }
    }
    .nav-item {
        margin-right: 12px;
        margin-left: 12px;
    }
    .nav-link {
        border-bottom: 2px solid transparent;
        font-family: Roboto-Medium;
        font-size: sz(22);
        color: #FFF !important;
        text-shadow: 0 2px 2px rgba(#000, 0.5);
    }
    .navbar-toggler {
        border-color: #fff !important;
    }
    .dropdown-menu {
        max-width: 16rem;
        min-width: 16rem;
        margin-top: 10px;
        padding: 0;
        border: none;
        border-radius: 0;
        box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.17);
        background: #FFFFFF;
    }
    .dropdown-item {
        padding: 12px 15px 10px;
        border-bottom: 1px solid $grey-6;
        color: $black-1;
        line-height: sz(22);
        letter-spacing: 0;
        &:last-child {
            border-bottom: none;
        }
        &:hover {
            border-bottom: 1px solid transparent;
            color: #fff;
            background: $red-3;
        }
    }
}

@media (min-width: 1200px) {
    .header {
        .nav-link {
            padding-bottom: 3px;
            border-bottom: 2px solid transparent;
            &:hover {
                border-bottom: 2px solid $red-3;
            }
        }
        .nav-item.show {
            .nav-link {
                border-bottom: 2px solid $red-3;
            }
        }
    }
}