.designer-about {
    padding: 60px 0 15px;
}

.designer {
    padding-top: 30px;
    padding-bottom: 6px;
    &__logo {
        width: 290px;
        margin-bottom: 30px;
    }
    &__text {
        @extend .info__text;
        margin-bottom: 24px;
    }
    &__video {
        @extend .award__item;
    }
    &__bg {
        @extend .award__bg;
        img {
            width: 60px;
            height: 60px;
        }
    }
}

@media (min-width:992px) {
    .designer {
        &__content {
            max-width: 340px;
        }
    }
}