.info {
    padding-top: 30px;
    text-align: center;
    &__content {
        max-width: 767px;
        margin: auto;
        padding: 0 15px;
    }
    &__title {
        margin-bottom: 30px;
        font-family: TeXGyreSchola-Regular;
        font-size: sz(40);
        line-height: sz(54);
        letter-spacing: 0;
        color: $red-3;
    }
    &__text {
        margin-bottom: 15px;
        font-family: Roboto-Regular;
        font-size: sz(18);
        line-height: sz(28);
        letter-spacing: 0;
        color: $black-1;
        a {
            color: $red-3;
            &:hover {
                color: $red-5;
            }
        }
    }
    .btn-red {
        font-family: Roboto-Medium;
        width: 139px;
        height: 50px;
        margin: 30px auto;
    }
}