.slide-main {
    position: relative;
    margin-bottom: 30px;
    .owl-nav {
        position: absolute;
        width: 100%;
        left: 0;
        top: 50%;
        margin-top: -25px;
    }
    .fa {
        padding: 0 25px;
        font-size: 50px;
        color: #fff;
    }
    .owl-prev,
    .owl-next {
        position: absolute;
    }
    .owl-next {
        right: 0;
    }
}