.course-slide {
    height: 100%;
    .carousel-inner {
        height: 100%;
    }
    .carousel-item {
        height: 100%;
    }
}

.course-info {
    padding-top: 60px;
    padding-bottom: 12px;
    border-bottom: 1px solid $grey-3;
}

.course-fe {
    padding-top: 30px;
    border-bottom: 1px solid $grey-3;
    &__item {
        margin-bottom: 30px;
    }
    &__img {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 97px;
        min-width: 97px;
        height: 97px;
        margin-right: 15px;
        background: $red-3;
        img {
            max-width: 80%;
            max-height: 80%;
        }
    }
    &__text1 {
        margin-bottom: 7px;
        a {
            font-family: TeXGyreSchola-Regular;
            font-size: sz(32);
            line-height: sz(43);
            color: $red-3;
            &:hover {
                color: $red-5;
            }
        }
    }
    &__text2 {
        font-size: sz(16);
        line-height: sz(23);
        color: $black-1;
    }
}

.course-video {
    padding-top: 30px;
    padding-bottom: 10px;
    &__item {
        @extend .award__item;
    }
    &__bg {
        @extend .award__bg;
        img {
            width: 90px;
            height: 90px;
        }
    }
}

@media (min-width: 768px) {
    .course-slide {
        .carousel-indicators {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            bottom: 50%;
            left: auto;
            z-index: 2;
            margin: 0;
            margin-bottom: -138px;
            li {
                width: 120px;
                height: 90px;
                margin: 2px 0 0;
                transition: all 0.5s;
                &.active {
                    width: 150px;
                }
            }
        }
    }
}

@media (max-width:767px) {
    .course-slide {
        .carousel-indicators {
            bottom: 10%;
            z-index: 2;
            li {
                width: 50px;
                height: 5px;
                background: rgba($grey-1, 0.7) !important;
                transition: all 0.5s;
                &.active {
                    background: rgba($green-1, 0.7) !important;
                }
            }
        }
    }
    .course-video {
        &__bg {
            img {
                width: 40px;
                height: 40px;
            }
        }
    }
}