.press-detail {
	background: #fff;
	margin-bottom: 30px;
	&__logo {
		text-align: center;
		border-bottom: 1px solid #D2D2D2;
		img {
			width: 357px;
			height: 130px;
		}
	}
	&__content {
		padding: 40px;
		font-size: 18px;
	}
	&__title {
		font-family: Roboto-Bold;
		font-size: 40px;
		color: #1A1A1A;
		margin-bottom: 20px;
	}
	&__img {
		margin-bottom: 20px;
		width: 50%;
	}
	&__text1 {
		margin-bottom: 20px;
	}
	&__text2 {
		font-family: Roboto-Bold;
		font-size: 24px;
		color: #0F0F0F;
		margin-bottom: 20px;
	}
}

@media (max-width: 991px) {
	.press-detail {
		&__img {
			width: 100%;
		}
	}
}
@media (max-width: 575px){
	.press-detail {
		&__content {
			padding: 20px 15px;
		}
		&__title {
			font-size: 32px;
		}
	}
}
@media (max-width: 400px) {
	.press-detail {
		&__logo {
			img {
				width: 100%;
			}
		}
	}
}